export function reading(lang) {
    return text => {
        const parsed = /^(.+?)_(.+?)$/i.exec(text);
        const [_, book, theRest] = parsed;

        return bibleNames[book][lang] + ' ' + theRest;
    };
}

export default function i18n(lang) {
    return string => {
        if (locales[string] && locales[string][lang]) {
            return locales[string][lang];
        } else {
            return '!!!***!!! ' + string + ' !!!***!!!';
        }
    };
}
