<script>
import { Ponomar, JDate, DatabaseHttp } from 'ponomar';
import { fade } from 'svelte/transition';

import Datepicker from 'svelte-calendar';
import { today, parseMMDDYYYY } from './util.js';
import i18n from './i18n';
import { reading as r18n } from './i18n.js';

let {year, month, day} = today();
let date = JDate.fromGregorian({year, month, day});
let dateFormatted = `${month}/${day}/${year}`;

$: [month, day, year] = dateFormatted.split('/').map(x=>+x);
$: date = JDate.fromGregorian({year, month, day});

function moveDay(delta) {
    const { year, month, day } = date.add(delta).toGregorian();
    dateFormatted = `${month}/${day}/${year}`;
}

let lang = 'cu';
let t = i18n('cu');
$: t = i18n(lang);
let r = r18n('cu');
$: r = r18n(lang);

const langMap = {
    cu: 'cu',
    ru: 'cu/ru',
    en: 'en',
}

async function sleep(millis) {
    return new Promise(resolve => setTimeout(resolve, millis));
}
async function loadDb(lang) {
    const response = await fetch('https://stvladnj.org/ponomar/db.json');
    const db = await response.json();
    return new DatabaseHttp(db).lang(langMap[lang]);
}

let db;
$: (async () => db = await loadDb(lang))();

let p;
$: p = db === undefined ? undefined : new Ponomar(db, date)

let cls;
$: cls = {
    cu: 'slavonic',
    ru: 'russian',
    en: 'english',
}[lang];

function* activeReadings(readings) {
    if (!readings) return;
    for (const office of [`vespers`, `matins`, `prime`, `terce`, `sexte`, `liturgy`, `none`]) {
        if (readings[office] === undefined || readings[office].length === 0) {
            continue;
        }
        yield [ office, readings[office] ];
    }
}

function formatName(name) {
    if (!name) return '';
    if (name.Nominative) return name.Nominative;
    if (name.Short) return name.short;
    return '';
}

function getMarker(type) {
    switch (+type) {
        case 1:
            return { text: '✺', red: false };
        case 2:
            return { text: '🕃', red: false };
        case 3:
            return { text: '🕃', red: true };
        case 4:
            return { text: '🕂', red: true };
        case 5:
            return { text: '🕁', red: true };
        case 6:
            case 7:
                case 8:
                    return { text: '🕀', red: true };
        default:
            return { text: '·', red: false };
    }
}

const DEFAULT_ICONS = [
    {name: 'default/1.jpeg', saint: {}},
    {name: 'default/2.jpeg', saint: {}},
    {name: 'default/3.jpeg', saint: {}},
];

function chooseIcon(db, p) {
    const saints = p.saints;
    const icons = [];
    if (saints !== undefined) {
        for (const saint of saints) {
            for (const icon of db.icons(saint.cid, ['cu', 'cu/ru', 'el', 'en'])) {
                icons.push({...icon, saint: (saint.name || {})});
            }
        }
    }

    if (icons.length === 0) {
        icons.push(...DEFAULT_ICONS);
    }

    const index = Math.floor(Math.random() * icons.length);

    return {
        src: 'dbIcons/' + icons[index].name,
        alt: formatName(icons[index].saint),
        title: formatName(icons[index].saint),
    };
}

let icon = {};
let imgFlip = false;
$: if (db !== undefined && p !== undefined) {
    icon = chooseIcon(db, p);
    imgFlip = !imgFlip;
}

</script>

<style>
.crossfade {
    display: block;
    position: relative;
}
img.icon {
    width: 100%;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin: auto auto;
}
.marker {
    font-size: 150%;
}
.red {
    color: red;
}
ul {
    list-style: none;
}
.section {
    height: 100vh;
}
</style>

<div class="container">
{#await db}
    <div class="row">
        <div class="col-lg-12">
	        {t('Loading...')}
        </div>
    </div>
{:then}
	<div class="row">
		<div class="col-lg-4">
			<div class="button-group">
				<button on:click={()=>moveDay(-1)}><i class="fas fa-arrow-circle-left"></i></button>
				<Datepicker bind:formattedSelected={dateFormatted} dateChosen={true}>
					<button id="date-selector">{dateFormatted}</button>
				</Datepicker>
				<button on:click={()=>moveDay(+1)}><i class="fas fa-arrow-circle-right"></i></button>
			</div>
            <div class="row section">
                <div class="col-lg-12">
                    <figure class="crossfade">
                    {#if imgFlip}
                        <img class="icon" transition:fade alt="" {...icon} />
                    {:else}
                        <img class="icon" transition:fade alt="" {...icon} />
                    {/if}
                    </figure>
                </div>
            </div>
		</div>
        <div class="col-lg-8">
            <div class="row">
        		<div class="col-lg-12">
                <button on:click={() => lang='cu'} class:primary={lang=='cux'}>cu</button>
                <button on:click={() => lang='ru'} class:primary={lang=='rux'}>ru</button>
                <button on:click={() => lang='en'} class:primary={lang=='enx'}>en</button>
                {#if p}
                <p class={cls}>{t(`fasting_${p.fastingCode}`)}</p>
                <p class={cls}>{t(`tone_${p.tone}`)}</p>
                {/if}
                </div>
            </div>
            <ul>
            {#if p}
            {#each p.saints as saint}
            <li class={cls}>
                <span class:marker={true} class:red={getMarker(saint.type).red} class={cls}>{getMarker(saint.type).text}</span>
                {@html formatName(saint.name)}
                <ul>
                {#each [...activeReadings(saint.readings)] as [serviceType, readings]}
                    <li class={cls}>{t(serviceType)}
                    <ul>
                    {#each readings as reading}
                        <li class={cls}>{r(reading.reading, reading.pericope)}</li>
                    {/each}
                    <ul></ul>
                    </li>
                {/each}
                </ul>
            </li>
            {/each}
            {/if}
            </ul>
        </div>
	</div>
{:catch error}
	<p style="color: red">{error.message}</p>
{/await}
</div>